* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
.heading {
  display: flex;
  justify-content: center;
  padding: 30px;
  font-size: 150%;
  background: rgb(241, 238, 238);
}
.containerAll {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #fff;
  background-attachment: fixed;
}
.cardContainer {
  position: relative;
  width: 1520px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.cardContainer .card {
  position: relative;
  width: 300px;
  height: 420px;
  background: #fff;
  margin: 20px;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}
.cardContainer:hover .card {
  -webkit-filter: blur(20px);
          filter: blur(20px);
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  opacity: 0.5;
}
.cardContainer .card:hover {
  -webkit-filter: blur(0px);
          filter: blur(0px);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  opacity: 1;
}

.cardContainer .card .circle {
  position: relative;
  width: 100%;
  height: 100%;
  background: #000;
  -webkit-clip-path: circle(180px at center 0);
          clip-path: circle(180px at center 0);
  text-align: center;
}
.cardContainer .card .circle h2 {
  color: #fff;
  font-size: 4.5rem;
  padding: 30px 0;
}
.cardContainer .card .content {
  padding: 20px;
  text-align: center;
}
.cardContainer .card .content .para h2 {
  position: absolute;
  bottom: 85px;
  right: 30px;
  left: 30px;
  color: #666;
}
.cardContainer .card .content .button .a {
  position: absolute;
  bottom: 10px;
  right: 80px;
  left: 80px;
  display: inline-block;
  padding: 10px 20px;
  background: #000;
  color: #fff;
  border-radius: 40px;
  text-decoration: none;
  margin-top: 20px;
}
.cardContainer .card:nth-child(1) .circle,
.cardContainer .card:nth-child(1) .content .button .a {
  background: rgb(27, 105, 179);
}
.cardContainer .card:nth-child(2) .circle,
.cardContainer .card:nth-child(2) .content .button .a {
  background: rgb(27, 105, 179);
}
.cardContainer .card:nth-child(3) .circle,
.cardContainer .card:nth-child(3) .content .button .a {
  background: rgb(27, 105, 179);
}
.cardContainer .card:nth-child(4) .circle,
.cardContainer .card:nth-child(4) .content .button .a {
  background: rgb(27, 105, 179);
}

